<template>
  <div class="right-right">
    <!-- 日历 -->
    <div class="calendar-data">
      <a-calendar
        :fullscreen="false"
        :locale="locale"
        :header-render="headerRender"
        @panelChange="onPanelChange"
      />
      <p>06:00 AM 某某事件某某事件某某事件</p>
      <div class="add-button" @click="addSchedule">
        <img src="@/assets/homeSvg/add.svg" alt />
      </div>
    </div>
    <!-- 快捷办公 -->
    <div class="fast-office">
      <div class="office-title">
        <span>快捷办公</span>
        <span>more +</span>
      </div>
      <div class="nav-layout">
        <div>
          <!-- <img src="@/assets/homeSvg/office/通讯录.svg" alt /> -->
          <span>通讯录</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/我的待办.svg" alt /> -->
          <span>我的待办</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/日程管理.svg" alt /> -->
          <span>日程管理</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/统计报表.svg" alt /> -->
          <span>统计报表</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/通讯录.svg" alt /> -->
          <span>通讯录</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/我的待办.svg" alt /> -->
          <span>我的待办</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/日程管理.svg" alt /> -->
          <span>日程管理</span>
        </div>
        <div>
          <!-- <img src="@/assets/homeSvg/office/统计报表.svg" alt /> -->
          <span>统计报表</span>
        </div>
      </div>
    </div>
    <!-- 站内消息 -->
    <div class="fast-office">
      <div class="office-title">
        <span>站内消息</span>
        <span>more +</span>
      </div>
      <div class="list-content">
        <div class="list-item">
          <p>国务院关税税则委员会关国务院关税...</p>
          <span>03.07</span>
        </div>
        <div class="list-item">
          <p>国务院关税税则委员会关国务院关税...</p>
          <span>03.07</span>
        </div>
        <div class="list-item">
          <p>国务院关税税则委员会关国务院关税...</p>
          <span>03.07</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "moment/locale/zh-cn";
export default {
  data() {
    return {
      value: new Date(),
      locale: {
        lang: {
          month: "月",
          year: "年"
        }
      }
      // isSchedule: false
    };
  },
  methods: {
    onPanelChange(value, mode) {
      console.log(value, mode);
    },
    headerRender({ value, type, onChange, onTypeChange }) {
      const start = 0;
      const end = 12;
      const monthOptions = [];

      const current = value.clone();
      const localeData = value.localeData();
      const months = [];
      for (let i = 0; i < 12; i++) {
        current.month(i);
        months.push(localeData.monthsShort(current));
      }

      for (let index = start; index < end; index++) {
        monthOptions.push(
          <a-select-option class="month-item" key={`${index}`}>
            {months[index]}
          </a-select-option>
        );
      }
      const month = value.month();
      const year = value.year();
      const options = [];
      for (let i = year - 10; i < year + 10; i += 1) {
        options.push(
          <a-select-option key={i} value={i} class="year-item">
            {i}
          </a-select-option>
        );
      }
      return (
        <div style={{ padding: "10px" }}>
          <a-row type="flex" justify="space-between">
            <a-col>
              <span>年份：</span>
              <a-select
                size="small"
                dropdownMatchSelectWidth={false}
                class="my-year-select"
                onChange={newYear => {
                  const now = value.clone().year(newYear);
                  onChange(now);
                }}
                value={String(year)}
              >
                {options}
              </a-select>
            </a-col>
            <a-col>
              <span>月份：</span>
              <a-select
                size="small"
                dropdownMatchSelectWidth={false}
                value={String(month)}
                onChange={selectedMonth => {
                  const newValue = value.clone();
                  newValue.month(parseInt(selectedMonth, 10));
                  onChange(newValue);
                }}
              >
                {monthOptions}
              </a-select>
            </a-col>
            <a-col>
              <a-radio-group
                size="small"
                onChange={e => onTypeChange(e.target.value)}
                value={type}
              >
                <a-radio-button value="month">月</a-radio-button>
                <a-radio-button value="year">年</a-radio-button>
              </a-radio-group>
            </a-col>
          </a-row>
        </div>
      );
    },
    // 添加日程
    addSchedule() {
      this.$store.dispatch("scheduleFrom", true);
    }
  }
};
</script>
<style lang="scss" scoped>
.right-right {
  margin: 10px 10px 10px 0;
}
.calendar-data {
  background: #fff;
  padding: 10px;
  box-sizing: border-box;
  p {
    font-size: 12px;
  }
  .add-button {
    display: flex;
    justify-content: center;
    img {
      width: 20px;
      height: 20px;
    }
  }
}

.fast-office {
  background: #fff;
  margin-top: 10px;
  padding: 10px;
  box-sizing: border-box;
  .office-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 40px;
    border-bottom: 1px solid #d5d5d5;
  }
  .nav-layout {
    display: flex;
    flex-wrap: wrap;
    margin-top: 5px;
    div {
      width: 25%;
      font-size: 12px;
      text-align: center;
      padding: 5px 0;
      img {
        width: 20px;
      }
      span {
        display: block;
      }
    }
  }
  .list-content {
    padding-top: 10px;
  }
  .list-item {
    font-size: 12px;
    display: flex;
    justify-content: space-between;
  }
}
</style>
<style>
.ant-fullcalendar-calendar-body {
  padding: 0 !important;
}
.ant-fullcalendar-today .ant-fullcalendar-value,
.ant-fullcalendar-month-panel-current-cell .ant-fullcalendar-value {
  border-radius: 50%;
}
.ant-fullcalendar-selected-day .ant-fullcalendar-value,
.ant-fullcalendar-month-panel-selected-cell .ant-fullcalendar-value {
  border-radius: 50%;
}
.ant-fullcalendar-column-header {
  line-height: 30px !important;
}
</style>


